import React from "react";
import s from "./Header.module.css";
// console.log(s);

const Header = function ({ children }) {
  return (
    <header className={s.header}>
      <nav>
        <a href="#">Header</a>
        <ul>
          <li>
            <a href="#">Home</a>
          </li>
          <li>
            <a href="#">About</a>
          </li>
          <li>
            <a href="#">Contacts</a>
          </li>
        </ul>
        {children}
      </nav>
    </header>
  );
};

export default Header;
